import './Layout.scss'
import Navbar from '../components/Navbar';
import { Link as LinkDOM } from 'react-router-dom';

function Layout() {
    return (
        <div className='layout-root'>
            <div className='layout-wrapper'>
            <Navbar/>
                <ul className='Layout'>
                    <li className='brick brick-React'>
                        <div>React : Une bibliothque JavaScript populaire permettant de crer des interfaces utilisateur dynamiques et performantes. (Trs utile)</div>
                    </li>
                    <li className='brick brick-Youtube'>
                        <div>YouTube : Une plateforme incontournable pour se former et dcouvrir des outils utiles pour le dveloppement web.</div>
                    </li>
                    <li className='brick brick-GSAP'>
                        <div>GSAP : Une bibliothque JavaScript puissante pour crer des animations fluides, interactives et complexes.</div>
                    </li>
                    <li className='brick brick-JSX'>
                        <div>JSX : Une syntaxe proche du HTML utilise avec React pour dcrire des interfaces utilisateur, en combinant HTML et XML de manire efficace.</div>
                    </li>
                    <li className='brick brick-SCSS'>
                        <div>SCSS : Un prprocesseur CSS qui enrichit les styles avec des fonctionnalits avances comme les variables, les mixins et l'imbrication de rgles, rendant le code plus lisible et maintenable.</div>
                    </li>
                    <li className='brick brick-Emmet'>
                        <div>Emmet : Un outil indispensable pour acclrer l'criture de HTML et CSS grce  des raccourcis (snippets) efficaces.</div>
                    </li>
                    <li className='brick brick-ChatGPT'>
                        <div>ChatGPT : Un assistant puissant pour poser des questions et apprendre rapidement de nouveaux concepts en programmation et au-del.</div>
                    </li>
                    <li className='brick brick-HTMLCSSJS'>
                        <div>HTML, CSS, JS : Les trois technologies fondamentales du dveloppement web frontend, utilises pour structurer, styliser et rendre interactif un site web.</div>
                    </li>
                </ul>
                <LinkDOM to="/" className='home-button-return'>
                    RETOURNER HOME
                </LinkDOM>
            </div>
        </div>
    );
}

export default Layout